
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');

body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  overflow: hidden;
  width: 1536px;
  height: 762px;
  background: url("./img/main/main-wallpaper.jpg") fixed center;
  background-size: cover;
}
*{
  box-sizing: border-box;
}
ul{
  list-style: none;
}
a{
  text-decoration: none;
}
button {
  outline: none;
  border: none;
  background-color: transparent;
}
.row {
  display: flex;
}
.d-flex {
  display: flex;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.justify-content-end {
  justify-content: end;
}
.h-100 {
  height: 100%;
}
.col-lg-6 {
  flex: 0 0 50%;
  max-width: 50%;
}
/*-- Navbar --*/
.nav-panel {
  position: fixed;
  width: 100%;
  height: 25px;
  left: 0;
  top: 0;
  background: linear-gradient(90.02deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0.01) 100%);
  mix-blend-mode: normal;
  -webkit-backdrop-filter: saturate(100%) blur(50px);
  backdrop-filter: saturate(100%) blur(50px);
}

.nav-container {
  position: absolute;
  width: 96.7%;
  display: flex;
  justify-content: space-between;
  margin: 0 25px;
}

/*-- Top Left Bar --*/

.apple {
  width: 382px;
  height: 25px;
  display: flex;
  justify-content: space-between;
  z-index: 2;
}

.applebtn {
  width: 40px;
  height: 25px;
  background: url("./img/navbar/apple.svg") no-repeat center;
}

.applebtn:hover {
  background-color: #1e75e7;
  cursor: pointer;
}

/* Apple Dropdown */
.apple-content {
  display: none;
  position: absolute;
  width: 250px;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(100px);
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  border-radius: 0 0 10px 10px;
  z-index: 1;
  top: 25px
}

.apple-content p {
  color: white;
  padding: 15px 25px;
  text-decoration: none;
  display: block;
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 0;
  margin: 0;
}

.apple-content p:hover {background-color: #1e75e7; cursor: pointer}

.apple:hover .apple-content {
  display: block;
}

.apple-sider-con {
  width: 350px;
  height: 25px;
  display: flex;
  justify-content: space-between;
}

.apple-text {
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 0;
  color: #FFFFFF;
  text-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
  align-self: center;
}

.apple-text:hover {
  color: #1e75e7;
  cursor: pointer;
}

.section-border1 {
  width: 250px;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

/*-- Top Right Bar --*/
.wi-fi-con {
  width: 309px;
  height: 25px;
  display: flex;
  justify-content: space-between;
}

.battery-svg {
