  height: 12px;
  background: url("./img/navbar/hotspot.svg") no-repeat;
  align-self: center;
}

.bright-svg {
  width: 12px;
  height: 12px;
  background: url("./img/navbar/bright.svg") no-repeat;
  align-self: center;
