}

.switch {
  width: 30px;
  height: 25px;
  display: flex;
  justify-content: center;
}

.switch:hover {
  background: #1e75e7;
  cursor: pointer;
}

.switch-svg {
  width: 15px;
  height: 15px;
  background: url("./img/navbar/switch.svg") no-repeat;
  align-self: center;
}

.switch-svg:hover {
  cursor: pointer;
}

.britness-img {
  width: 21px;
  height: 11px;
  background: url("./img/navbar/kbrite.svg") no-repeat;
  align-self: center;
}

.airplay-svg {
  width: 17px;
  height: 15px;
  background: url("./img/navbar/airplay.svg") no-repeat;
  align-self: center;
}

.switch-content {
  display: none;
  position: absolute;
  width: 325px;
  height: 405px;
  right: -15px;
  top: 25px;
  background: rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(100px);
  border-radius: 20px;
  z-index: 2;
}

.switch-container1 {
  width: 295px;
  height: 150px;
  display: flex;
  justify-content: space-between;
