}

.second-inner {
  width: 135px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.third-inner {
  width: 135px;
  height: 65px;
  display: flex;
  justify-content: space-between;
}

.inner-con1 {
  width: 145px;
  height: 150px;
  padding: 15px 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.inner-con2 {
  width: 135px;
  height: 70px;
  display: flex;
  align-items: center;
  padding: 10px;
}

.inner-con3 {
  width: 60px;
  height: 65px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 0;
}

.switch:hover .switch-content {
  display: block;
}

.invisible-con1 {
  width: auto;
  height: 30px;
  display: flex;
}

.blue-circle {
  width: 30px;
  height: 30px;
  border-radius: 100%;
  background: #017AFF;
  display: flex;
  align-content: center;
  justify-content: center;
  margin-right: 10px;
}

.circle-text-con {
  width: auto;
  height: 25px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.head-tex {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 0;
  color: #070808;
  text-align: left;
  margin: 0;
}

.tex-bottom {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  font-size: 10px;
  line-height: 0;
  text-align: left;
  color: #8C8C8D;
  margin: 0;
}

.time {
  font-family: 'Poppins', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 0;
  color: #FFFFFF;
  align-self: center;
}

.kbtex {
  text-align: center;
  font-size: 9px;
}

.br-slider {
  width: 95%;
  height: 17px;
  display: flex;
  position: relative;
}
.range {
  width: 100%;
  height: 20px;
  -webkit-appearance: none;
  background: #89B4CF;
  outline: none;
  border-radius: 15px;
  overflow: hidden;
  border: 1px solid #6DA5C9;
  margin: 0;
}

.range::-moz-range-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  width: 18px;
  height: 18px;
  border: none;
  border-radius: 50%;
  background: #FFFFFF;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.25);
}

.range::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 20px;
  height: 20px;
  border: none;
  border-radius: 50%;
  background: #FFFFFF;
  cursor: pointer;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.25);
}

.range::-moz-range-progress {
  background-color: #FFFFFF;
  height: 20px;
}

.airs-circle {
  width: 24px;
  height: 24px;
  margin-right: 0;
  margin-left: 10px;
  align-self: center;
  background: #89B4CFD0;
}

.vol-wid {
  width: 88%;
}

/*-- Middle Screen --*/
.screen-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 25px;
  display: flex;
  z-index: 1;
}

.app-img-txt-con {
  width: auto;
  height: 70px;
}
.app-column {
  width: 126px;
  height: 400px;
  display: grid;
  margin-top: 25px;
}

.center-img {
  display: inline-block;
}

.app-txt {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  font-size: 12px;
  line-height: 1;
  color: #FFFFFF;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.49);
}


/*-- Bottom App Container --*/
.bt-con {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: center;
  bottom: 5px;
  z-index: 2;
}

.bottom-container {
  width: 685px;
  height: 75px;
  background: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: saturate(100%) blur(50px);
  backdrop-filter: saturate(100%) blur(50px);
  border-radius: 20px;
}

.bt-inner-con {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 25px;
}

.app-img {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  padding: 0;
}

.finder-img {
  background: url("./img/appbar/finder.svg");
}
.safari-img {
  background: url("./img/appbar/safari.svg");
}
.user-img {
  background: url("./img/appbar/user.svg");
}
.linkedin-img {
  background: url("./img/appbar/linkedin.svg");
}
.github-img {
  background: url("./img/appbar/github.svg");
}
.message-img {
  background: url("./img/appbar/message.svg");
}
.mail-img {
  background: url("./img/appbar/mail.svg");
}
.map-img {
  background: url("./img/appbar/maps.svg");
}
.appstore-img {
  background: url("./img/appbar/appstore.svg");
}
.settings-img {
  background: url("./img/appbar/settings.svg");
}
.recyclebin-img {
  background: url("./img/appbar/emprecycle.svg");
}

.section-border {
  height: 50px;
  border: 1px solid rgba(0, 0, 0, 0.3);
}
/*----------------------------------------------------
                 About Kshitij
------------------------------------------------------*/
.about-container {
  display: none;
  width: 876px;
  height: 500px;
  background: #FFFFFF;
  border-radius: 10px;
  z-index: 3;
  position: absolute;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.taskbar-container {
  position: absolute;
  width: 100%;
  height: 30px;
  left: 0;
  top: 0;
  background: #FFFFFF;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 10px 10px 0 0;
  display: flex;
  align-items: center;
  z-index: 4;
}
.close-bar {
  position: absolute;
  width: 56px;
  height: auto;
  left: 30px;
  justify-content: space-between;
  display: flex;
  z-index: 6;
}
.closebar-button {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.25);
}
.close-button {
  background: #FB675C;
}
.close-button:hover .close-svg {
  background: url("components/img/extra/close.svg") no-repeat;
  width: 10px;
    position: absolute;
    height: 10px;
    left: 7%;
    top: 30%;
}
.minimize-button {
  background: #FECB25;
}
.minimize-button:hover .minimize-svg {
  background: url("components/img/extra/minimize.svg") no-repeat;
  width: 10px;
    position: absolute;
    height: 10px;
    left: 45%;
    top: 50%;
}
.maxmize-button {
  background: #09cf52;
}
.maxmize-button:disabled, .maxmize-button:disabled .maximize-svg {
  opacity: 0.3;
  cursor: auto;
}
.maxmize-button:hover .maximize-svg {
  background: url("components/img/extra/maximize.svg") no-repeat;
  width: 10px;
    position: absolute;
    height: 10px;
    left: 82%;
    top: 18%;
}
.about-menu-container {
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  justify-content: center;
  z-index: 5;
}
.about-menu-text{
  font-family: 'Poppins', sans-serif;
